const categories = ["رجالي", "نسائي", "أطفال"];

const products = [
  {
    name: "بنطال جينز رجالي ضيق",
    price: "$49.99",
    image: "/products/mens-jeans.jpg",
    category: "رجالي",
  },
  {
    name: "فستان صيفي نسائي",
    price: "$39.99",
    image: "/products/womens-dress.jpg",
    category: "نسائي",
  },
  {
    name: "تيشيرت أطفال بطبعة",
    price: "$19.99",
    image: "/products/kids-tee.jpg",
    category: "أطفال",
  },
  {
    name: "قميص رجالي كاجوال",
    price: "$34.99",
    image: "/products/mens-shirt.jpg",
    category: "رجالي",
  },
  {
    name: "ليجن يوغا نسائي",
    price: "$29.99",
    image: "/products/womens-leggings.jpg",
    category: "نسائي",
  },
  {
    name: "جاكيت جينز أطفال",
    price: "$39.99",
    image: "/products/kids-jacket.jpg",
    category: "أطفال",
  },
];

export default function NewArrivalsPage() {
  const [selectedCategory, setSelectedCategory] = useState(null);

  const filteredProducts = selectedCategory
    ? products.filter((product) => product.category === selectedCategory)
    : products;

  return (
    <div className="p-8 bg-[#f1f1f2] min-h-screen" dir="rtl">
      <h1 className="text-3xl font-bold mb-4">وصل حديثًا</h1>
      <div className="flex gap-4 mb-6">
        {categories.map((category) => (
          <Button
            key={category}
            variant="outline"
            className={`rounded-full ${selectedCategory === category ? "bg-muted" : ""}`}
            onClick={() =>
              setSelectedCategory(
                selectedCategory === category ? null : category
              )
            }
          >
            {category}
          </Button>
        ))}
      </div>
      <div className="grid grid-cols-2 sm:grid-cols-3 md:grid-cols-4 gap-6">
        {filteredProducts.map((product, index) => (
          <div key={index} className="flex flex-col gap-2 text-right">
            <div className="rounded-lg overflow-hidden shadow-sm">
              <Image
                src={product.image}
                alt={product.name}
                width={300}
                height={300}
                className="object-cover w-full h-[300px]"
              />
            </div>
            <div className="text-sm font-medium text-black">{product.name}</div>
            <div className="text-sm text-gray-600">{product.price}</div>
          </div>
        ))}
      </div>
    </div>
  );
}
